$bigBoxLinkImg: '.big-box-link-img';

#{$bigBoxLinkImg} {
  position: relative;
  display: inline-block;
  background-color: #f9f9fb;
  &__wrapper {
    display: flex;
    height: 100%;
  }
  &__link {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    &:hover,
    &:focus {
      ~ #{$bigBoxLinkImg} {
        &__img-wrapper {
          #{$bigBoxLinkImg} {
            &__img {
              transform: scale(1.05);
            }
          }
        }
        &__content {
          #{$bigBoxLinkImg} {
            &__title {
              color: $blue1;
              text-decoration-color: $blue1;
            }
          }
        }
      }
    }
  }
  &__mobile-content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    display: none;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

    padding: 20px;

    background: linear-gradient(180deg, rgba(0, 18, 50, 0) 33.16%, rgba(0, 18, 50, 0.18) 51.61%, rgba(0, 18, 50, 0.26) 72.89%, rgba(0, 18, 50, 0.33) 100%);
    color: #fff;

    @include media('<=desktop') {
      display: flex;
    }
  }
  &__mobile-tag {
    position: relative;
    margin-bottom: 5px;
    z-index: 10;
  }
  &__mobile-title {
    margin-bottom: 10px;
  }
  &__mobile-date {
    font-size: 10px;
    line-height: 16px;
  }
  &__img-wrapper {
    width: 270px;
    flex: 0 0 270px;
    overflow: hidden;
    @include media('<=desktop') {
      width: 100%;
      flex: 0 0 100%;
    }
  }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: all 0.3s ease;
    will-change: transform;
  }
  &__content {
    display: flex;
    flex-direction: column;
    padding: 30px;
    @include media('<=desktop') {
      display: none;
    }
  }
  &__title {
    margin-bottom: 12px;
    text-underline-offset: 2px;
    text-decoration: underline;
    text-decoration-color: transparent;
    transition: all 0.3s ease;
  }
  &__excerpt {
    max-width: 90%;
    margin-bottom: auto;
  }
  &__tag-date {
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-top: 10px;
  }
  &__tag {
    position: relative;
    margin-right: 20px;
    font-size: 14px;
    color: $blue1;
    text-decoration: underline;
    text-decoration-color: transparent;
    text-underline-offset: 2px;
    transition: all 0.3s ease;
    z-index: 10;
    &:hover,
    &:focus {
      text-decoration-color: $blue1;
    }
  }
  &__date {
    font-size: 12px;
  }

  &_alt {
    #{$bigBoxLinkImg} {
      &__title {
        margin-bottom: 10px;
      }
      &__content {
        padding: 20px 20px 20px 30px;
      }
      &__excerpt {
        max-width: unset;
      }
      &__date {
        color: #adb4c6;
      }
    }
  }

  &_article {
    #{$bigBoxLinkImg} {
      &__img-wrapper {
        width: 200px;
        flex: 0 0 200px;
        height: 200px;
        overflow: hidden;
        @include media('<=desktop') {
          width: 100%;
          flex: 1 1 100%;
          height: 240px;
        }
      }
      &__content {
        padding: 20px 30px 30px 30px;
      }
      &__title {
        margin-bottom: 0;
        font-size: 20px;
        line-height: 30px;
        font-weight: 400;
        
        overflow: hidden;
        -webkit-line-clamp: 3;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        text-overflow: ellipsis;
      }
      &__mobile-title {
        font-size: 14px;
        line-height: 21px;
        font-weight: 600;

        overflow: hidden;
        -webkit-line-clamp: 4;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        text-overflow: ellipsis;
      }
      &__mobile-tag {
        position: absolute;
        top: 20px;
        right: 20px;
        min-height: 24px;
        border: 0;
        background-color: $blue1;
        color: #fff;
        font-size: 10px;
      }
      &__tag-date {
        margin-top: auto;
      }
      &__tag {
        padding-left: 10px;
        padding-right: 10px;
        border: 1px solid $blue1;
        border-radius: 50px;
      }
      &__date {
        font-size: 14px;
      }
    }
  }
}
